:root {
  --flex-items: 4;
  --flex-items-gap: 10px;
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
  gap: var(--flex-items-gap);
}

.flex-container-item {
  flex-basis: calc(
    (100% - (var(--flex-items) - 1) * var(--flex-items-gap)) / var(--flex-items)
  );
}

.flex-container-item img {
  display: block;
  /* height: 200px; */
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

#search-form {
  display: flex;
  justify-content: center;
  background: rgb(24, 8, 114);
  padding: 15px 10px;

  box-shadow: 0 0 2px #000;
}

.gallery {
  padding: var(--flex-items-gap);
}

@media screen and (max-width: 375px) {
  .gallery {
    --flex-items: 1;
    justify-content: center;
  }
}

@media screen and (max-width: 768px) {
  .gallery {
    --flex-items: 2;
  }
}

@media screen and (max-width: 1000px) {
  .gallery {
    --flex-items: 3;
  }
}

@media screen and (max-width: 1220px) {
  .gallery {
    --flex-items: 4;
  }
}

.gallery__link {
  color: currentColor;
  text-decoration: none;
}

.gallery__link img{
  margin: 0 auto;
  width: 100%;
  height: auto;
}

@media screen and (min-width: 500px) {
  .gallery__link img {
    width: 100%;
    height: 200px;
  }
}

