:root {
  --flex-items: 4;
  --flex-items-gap: 10px;
}

.flex-container {
  gap: var(--flex-items-gap);
  flex-wrap: wrap;
  display: flex;
}

.flex-container-item {
  flex-basis: calc( (100% - (var(--flex-items)  - 1) * var(--flex-items-gap) ) / var(--flex-items) );
}

.flex-container-item img {
  display: block;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

#search-form {
  background: #180872;
  justify-content: center;
  padding: 15px 10px;
  display: flex;
  box-shadow: 0 0 2px #000;
}

.gallery {
  padding: var(--flex-items-gap);
}

@media screen and (max-width: 375px) {
  .gallery {
    --flex-items: 1;
    justify-content: center;
  }
}

@media screen and (max-width: 768px) {
  .gallery {
    --flex-items: 2;
  }
}

@media screen and (max-width: 1000px) {
  .gallery {
    --flex-items: 3;
  }
}

@media screen and (max-width: 1220px) {
  .gallery {
    --flex-items: 4;
  }
}

.gallery__link {
  color: currentColor;
  text-decoration: none;
}

.gallery__link img {
  width: 100%;
  height: auto;
  margin: 0 auto;
}

@media screen and (min-width: 500px) {
  .gallery__link img {
    width: 100%;
    height: 200px;
  }
}

.info {
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  transition-property: box-shadow;
  transition-duration: .3s;
  transition-timing-function: ease;
  display: flex;
  box-shadow: 0 0 3px rgba(100, 100, 100, .5);
}

.gallery__link:hover .info {
  box-shadow: 0 0 3px #646464;
}

.search-button {
  background-image: url("icon.b419d2f1.png");
  background-position: center;
  background-repeat: no-repeat;
  padding: 0 20px;
}

.info-item {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  display: flex;
}

.load-more {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background: rgba(0, 150, 0, .7);
  border: none;
  border-radius: 5px;
  margin: 0 auto 10px;
  padding: 10px 20px;
  font-size: 20px;
  display: none;
}

.input-group > .form-control {
  width: 100%;
  max-width: 300px;
  flex: none;
}

